.card  {
    transition: box-shadow .3s;
    transition: all .3s ease-in-out; 
    border: 4px solid #dfdfdf;
}


.card:hover {
    box-shadow: 10px 15px 15px #456abb77;
    z-index: 1000;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

body {
    background-image: url(/Clicky-Game/static/media/tic-tac-toe.a2e9b7c1.png);
}
nav {
    background: #fff8a7;
}

.navbar-brand {
    color: #456abb;
    font-size: 44px;
    font-family: 'Bungee Shade', cursive;
    letter-spacing: 2.5px;
}

.instructions {
    font-size: 24px;
    color: #456abb;
    -webkit-animation: pulsate 2s ease-out;
    -webkit-animation-iteration-count: infinite; 
    opacity: 0.5;
    font-weight: bold;
    background: #ffffff80;
}
@-webkit-keyframes pulsate {
    0% { 
        opacity: 0.5;
    }
    50% { 
        opacity: 1.0;
    }
    100% { 
        opacity: 0.5;
    }
}

.gameTextbox {
    background: #456abb;
    border-top: 6px solid #fff8a7;
    border-left: 6px solid #fff8a7;
    border-right: 6px solid #fff8a7;
    color: #ffffff;
}

.gifBox {
    border-bottom: 6px solid #fff8a7;
    border-left: 6px solid #fff8a7;
    border-right: 6px solid #fff8a7;
}

.score {
    font-size: 24px;
}

.gameHeadline {
    font-size: 24px;
}
